    name: 'post-preview',
    props: {
        post: Object
    },
    data() {
        return {
            hovered: false
        }
    },
    methods: {
        get_date(date) {
            return dayjs.unix(date).locale('ru').format('DD MMM YYYY')
        }
    }
}
